function run(...funcArray) {
    return funcArray.reduce((value, func) => func(value));
}

function f1(value) {
    console.log('f1', value);
    return value * value;
}

function f2(value) {
    console.log('f2', value);
    return value + value;
}

function f3(value) {
    console.log('f3', value);

    return value + 1;
}

const result = run(2, f1, f2, f3);

console.log(result);
